*:before,
*:after {
  box-sizing: border-box;
}

nav .menu {
  position: fixed;
  left: -100px;
  top: -100px;
  z-index: 35;
  width: 200px;
  height: 200px;
  background: var(--details);
  border-radius: 50%;
  transition: .5s ease-in-out;
  box-shadow: 0 0 0 0 var(--details), 0 0 0 0 var(--details);
  cursor: pointer;
}
nav .hamburger {
  position: absolute;
  left: 120px;
  top: 132px;
  width: 40px;
  height: 2px;
  background-color: var(--main_light);
  display: block;
  transform-origin: center;
  transition: .5s ease-in-out;
  box-shadow: 0 0 10px var(--neon);
}
nav .menu:hover .hamburger,
nav .menu:hover .hamburger::after,
nav .menu:hover .hamburger::before {
  background-color: var(--neon);
}
.hamburger::after,
.hamburger::before {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: var(--main_light);
  transition: .5s ease-in-out;
  box-shadow: 0 0 10px var(--neon);
  transition: .5s ease-in-out;
}
.hamburger::before {
  top: -10px;
}
.hamburger::after {
  bottom: -10px;
}
.nav_input {
  display: none;
}
.nav_input:checked + .menu {
  box-shadow: 0 0 0 100vw var(--details), 0 0 0 100vh var(--details);
  border-radius: 0;
}
.nav_input:checked + .menu .hamburger {
  transform: rotate(45deg);
}
.nav_input:checked + .menu .hamburger::after {
  transform: rotate(90deg);
  bottom: 0
}
.nav_input:checked + .menu .hamburger::before {
  transform: rotate(90deg);
  top: 0
}
.nav_input:checked + .menu + ul {
  opacity: 1;
  transform: translate(-50%,-50%);
}
nav ul {
  z-index: 40;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-400%);
  opacity: 1;
  transition: .25s 0s ease-in-out;
}
nav ul li {
  text-align: center;
}
nav .link {
  display: block;
  color: var(--main_light);
  font-size: 3rem;
  text-shadow: 0 0 10px var(--neon);
  margin: 0.5rem 0;
  transition: .4s;
}
nav .link:hover {
  color: var(--neon);
}

@media (max-width: 500px) {
  nav .link {
    font-size: 2em;
  }
