  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.5, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.5, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes jello {
  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.5, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.5, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}

@-webkit-keyframes button1 {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes button1 {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@-webkit-keyframes button2 {
  0% { transform: translateY(-100%); }
  100% { transform: translateY(100%); }
}

@keyframes button2 {
  0% { transform: translateY(-100%); }
  100% { transform: translateY(100%); }
}

@-webkit-keyframes button3 {
  0% { transform: translateX(100%); }
  100% { transform: translateX(-100%); }
}

@keyframes button3 {
  0% { transform: translateX(100%); }
  100% { transform: translateX(-100%); }
}

@-webkit-keyframes button4 {
  0% { transform: translateY(100%); }
  100% { transform: translateY(-100%); }
}

@keyframes button4 {
  0% { transform: translateY(100%); }
  100% { transform: translateY(-100%); }
}

/* --General-- */
:root {
  --main_font: 'Sora', Verdana, sans-serif;
  --main_dark: rgb(40,40,43);
  --main_light: rgb(255,255,255);
  --shadow: rgb(206,200,205);
  --neon: rgb(255,170,33);
  --details: rgb(94,45,150);
}
* {
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  color: rgb(255,255,255);
  color: var(--main_light);
  min-height: 100vh;
  background-color: rgb(40,40,43);
  background-color: var(--main_dark);
  min-width: 350px;
}
a, button {
  text-decoration: none;
  color: rgb(255,255,255);
  color: var(--main_light);
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
}
button {
  border: none;
  outline: none;
  background-color: inherit;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
}
ul {
  list-style: none;
}
.app {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
}
.container {
  width: 90%;
  margin-left: 10%;
  position: relative;
  min-height: 95vh;
  height: auto;
  overflow: hidden;
}
.highlighted {
  transition: 0.4s ease-in-out;
}
.jello_title h1 {
  font-size: 3rem;
  letter-spacing: 0.2rem;
  cursor: default;
}
.jello_title h1 span {
  display: inline-block;
  transition: .5s ease-in-out;
}
.jello_title h1 span:hover {
  -webkit-animation: jello 0.9s ease-in-out both;
          animation: jello 0.9s ease-in-out both;
  text-shadow: 0 0 20px rgb(255,170,33);
  text-shadow: 0 0 20px var(--neon);
}
.button {
  display: inline-block;
  padding: 15px 30px;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
  transition: .2s ease-out;
}
.button:hover {
  background-color: rgb(94,45,150);
  background-color: var(--details);
  box-shadow: 0 0 10px rgb(94,45,150), 0 0 40px rgb(94,45,150), 0 0 80px rgb(94,45,150);
  box-shadow: 0 0 10px var(--details), 0 0 40px var(--details), 0 0 80px var(--details); 
}
.button span:nth-child(1) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-image: linear-gradient(to right, transparent, rgb(94,45,150));
  background-image: linear-gradient(to right, transparent, var(--details));
  -webkit-animation: button1 1s linear infinite;
          animation: button1 1s linear infinite;
}
.button span:nth-child(2) {
  position: absolute;
  top: 0;
  right: 0;
  width: 2px;
  height: 100%;
  background-image: linear-gradient(to bottom, transparent, rgb(94,45,150));
  background-image: linear-gradient(to bottom, transparent, var(--details));
  -webkit-animation: button2 1s linear infinite;
          animation: button2 1s linear infinite;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}
.button span:nth-child(3) {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-image: linear-gradient(to left, transparent, rgb(94,45,150));
  background-image: linear-gradient(to left, transparent, var(--details));
  -webkit-animation: button3 1s linear infinite;
          animation: button3 1s linear infinite;
}
.button span:nth-child(4) {
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background-image: linear-gradient(to top, transparent, rgb(94,45,150));
  background-image: linear-gradient(to top, transparent, var(--details));
  -webkit-animation: button4 1s linear infinite;
          animation: button4 1s linear infinite;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}
.hide {
  display: none;
}
@media (max-width: 500px) {
  .container {
    margin-left: 0%;
    width: 100%;
  }
  .jello_title h1 {
    font-size: 2rem;
    letter-spacing: 0;
    text-align: right;
  }
}
